.SIAB-header {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    background-image: 
    linear-gradient(rgb(0,0,0, 0.5), rgb(0,0,0, 0.5)),
    url('../../Assets/SIAB/Header.png');
    background-position: 50% 55%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 20em;
    max-height: 20em;
    gap: 1.5em;
    h1 {
        color: white;
        font-size: 4em;
    }
    h2 {
        color: white;
        font-weight: 400;
        font-style: italic;
        font-size: 1.15em;
    }
}

.SIAB-about {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    padding-left: 50px;
    padding-right: 50px;
    gap: 2em;
    h2 {
        text-align: center;
        font-weight: 400;
        font-size: 1.2em;
    }
    .underline {
        text-decoration-line: underline;
    }
    iframe {
        border: 2px solid black;
        border-radius: 10px;
    }
    a {
        margin-top: 1em;
        background-color: inherit;
        text-align: center;
        text-decoration: none;
        border: 2px solid black;
        padding: 0.5em;
        max-width: 15em;
        font-weight: bold;
        color: black;
        transition: all .25s;
        &:hover {
            background-color: rgb(255, 230, 0);
        }
    }
}

.SIAB-participate {
    background-color: rgb(255, 230, 0);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    padding-left: 50px;
    padding-right: 50px;
    gap: 2em;
    h2 {
        text-align: center;
        font-weight: 400;
        font-size: 1.2em;
        a {
            text-decoration: underline;

        }
    }
    .QRCode {
        max-height: 40vh;
    }
    .row {
        display: flex;
        flex-direction: row;
        gap: 3em;
    }
    .ImageSlider {
        .image {
            width: 800px;
        }
    }
}