.sponsor-header {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    background-image: 
    linear-gradient(rgb(0,0,0, 0.5), rgb(0,0,0, 0.5)),
    url('../../Assets/Sponsors/PageContent/Header.JPG');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 20em;
    max-height: 20em;
    gap: 1.5em;
    h1 {
        color: white;
        font-size: 4em;
    }
    h2 {
        color: white;
        font-weight: 400;
        font-style: italic;
        font-size: 1.15em;
    }
}

.sponsor-instructions {
    display: flex;
    justify-content: center;
    margin: 1em 0em;
    font-weight: 600;
    font-size: 1.75em;
}

.sponsor-container {
    display: flex;                                                             
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 5vw;
    margin-top: 2.5vw;
    .row {
        display: flex;
        flex-direction: row;
    }
    .sponsor-card {
        margin: 2vw 3vw;
    }
}

.sponsor-footer {
    display: flex;
    flex-direction: row;
    background-color: rgb(255, 217, 0);
    padding-top: 3em;
    padding-left: 7em;
    padding-right: 7em;
    padding-bottom: 0;
    .graphic {
        height: 16.9em;
        width: 27%;
        
    }
    .column {
        display: flex;
        flex-direction: column;
        gap: 1em;
        a {
            background-color: inherit;
            text-align: center;
            text-decoration: none;
            border: 2px solid black;
            padding: 0.5em 0em;
            max-width: 10em;
            font-weight: bold;
            color: black;
            transition: all .25s;
            &:hover {
                background-color: white;
            }
        }
        p {
            max-width: 60%;
            font-size: 1.25em;
            font-weight: 300;
        }
    }
}

@media (max-width: 925px) {
    .sponsor-header {
        h1 { font-size: 9vw }
        h2 { font-size: 2vw }
    }
    .sponsor-container {
        
    }
}
