.home-one {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    padding-top: 150px;
    height: 90vh;
    gap: 6em;
    background-image: linear-gradient(rgb(0,0,0, 0.7), rgb(0,0,0, 0.7)), url('../../Assets/Home/Background.jpg');
    background-position: 50% 20%;
    background-repeat: no-repeat;
    background-size: cover;
    .Typewriter {
        font-size: 3em;
        color: rgb(255, 196, 0);
        font-weight: 700;
    }
    a {
        transition: all .25s;
        color: white;
        text-decoration: none;
        font-size: 1.5em;
        border: 2px solid white;
        padding: 5px;
        border-radius: 5px;
        &:hover {
            transition: all .25s;
            background-color: white;
            color: black;
        }
    }
    h1 {
        color: white;
        font-size: 7em;
    }
}

.home-two {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
    padding-top: 50px;
    background-color: rgb(255, 196, 0);
    color: black;
    .row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-left: 100px;
        padding-right: 100px;
        gap: 1em;
    }
    .column {
        display: flex;
        flex-direction: column;
        align-content: center;
    }
    p {
        max-width: 90%;
        font-size: 1.5em;
        padding-bottom: 2em;;
    }
    img {
        height: 350px;
    }
    .ImageSlider {
        img {
            
        }
    }
    hr {
        margin: 0 auto;
        margin-top: 1em;
        border-color: black;
        width: 10em;
        margin-bottom: 3em;
    }
    a {
        margin: 3.5em 0em;
        margin-bottom: 2em;
        font-size: 1.5em;
        border: 1px solid black;
        padding: 20px;
        transition: all .25s;
        color:black;
        &:hover {
            transition: all .25s;
            background-color: white;
        }
        text-decoration: none;;
    }
}

.home-three {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    hr {
        margin: 0 auto;
        margin-top: 1em;
        border-color: black;
        width: 10em;
        margin-bottom: 2em;
    }
    .flex-grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 2em;
        .row {
            display: flex;
            flex-direction: row;
        }
        .col {
            display: flex;
            flex-direction: row;
            flex: 1;
        }
        .sponsor-card {
            margin: 2vw 3vw;
        }
    }
}
