.robocamp-header {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    background-image: 
    linear-gradient(rgb(0,0,0, 0.5), rgb(0,0,0, 0.5)),
    url('../../Assets/RoboCamp/HeaderBackground.JPG');
    background-position: 50% 55%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 20em;
    max-height: 20em;
    gap: 1.5em;
    h1 {
        color: white;
        font-size: 4em;
    }
    h2 {
        display: flex;
        color: white;
        font-weight: 400;
        font-style: italic;
        font-size: 1.15em;
    }
}

.robocamp-about {
    display: flex;
    flex-direction: column;
    padding: 5em 12.5em;
    .header {
        display: flex;
        align-items: center;
        margin-bottom: 0.25em;
    }
    .horizontal {
        display: flex;
        flex-direction: row;
        gap: 5em;
    }
    h2 {
        margin-top: 2em;
        max-width: 35em;
        font-weight: 400;
        font-size: 1.25em;
    }
    a {
        margin-top: 1em;
        background-color: inherit;
        text-align: center;
        text-decoration: none;
        border: 2px solid black;
        padding: 0.5em 0em;
        max-width: 15em;
        font-weight: bold;
        color: black;
        transition: all .25s;
        &:hover {
            background-color: rgb(255, 230, 0);
        }
    }
}

.robocamp-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2em 10em; 
    background-color: rgb(255, 230, 0);
    scroll-margin-top: 3em;
    padding-bottom: 4em;
    .horizontal {
        margin-top: 7em;
        display: flex;
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        gap: 20em;
    }
}

.robocamp-FAQ {
    display: flex;
    flex-direction: column;
    padding: 5em 12.5em;
    align-items: center;
    h1 {
        margin-bottom: 0.5em;
    }
    .desc {
        font-size: 20px;
        font-style: italic;
        font-weight: 400;
        margin-bottom: 2em;
    }
    .accordion {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: rgb(245, 245, 245);
        border: 1px solid rgb(197, 197, 197);
        width: 85%;
        font-size: 1.25em;
        color: rgb(75, 75, 75);
        text-align: left;
        padding: 0.5em 2em;
        transition: 0.5s;
    }

    .active, .accordion:hover {
        background-color: rgb(226, 226, 226);
    }

    .panel {
        max-height: 0;
        width: 85%;
        margin-top: 1em;
        overflow: hidden;
        transition: max-height 0.2s ease-out;
        margin-bottom: 1em;
    }
}